// @import "node_modules/bootstrap-sass/assets/stylesheets/bootstrap";

.card {
  border: 1px solid #eee;
  padding: 1rem;
  margin: 1rem;
  background-color: #fff;
  height: 8rem;
  overflow: hidden;

  h3 {
    font-size: 1.25rem;
  }

  span {
    display: none;
  }
}

.update {
  display: none;
}

.cards {
  max-height: 30rem;
  overflow-y: scroll;
}

.body {
  border: 1px solid #eee;
  height: 100%;
  max-height: 30rem;
  min-height: 25rem;
  overflow-y: scroll;
  outline: none;
  margin-bottom: 1rem;
  padding: 1rem;
}

.title {
  font-size: 2rem;
  min-height: 2rem;
  border: 1px solid #eee;
  outline: none;
  margin-bottom: 1rem;
  padding: 0.5rem;
}